import React, { useState } from "react";
import "./navbar.css"

export const Sidebar = (props: any) => {
  const [sidebar, setSidebar] = useState<null | boolean>(null);
  const CloseMenu = require("../../assets/close.svg") as string;

  return (
    <div className={"navigation " + (props.sidebar ? "navigation-open" : "")}>
      <br></br>
      <div className="sidebar-menu">
        <div>
          <span className="sidebar-heading">I'm Listening</span>
        </div>
        <div>
          <img
            onClick={() => setSidebar(!sidebar)}
            className="close-menu_image"
            src={CloseMenu}
            alt="Close menu"
          />
        </div>
      </div>
      <hr className="sidebar-divider" />
      <ul>
        <li>
          <a href="">Home</a>
        </li>
        <li>
          <a href="">Latest</a>
        </li>
        <li>
          <a href="">Popular</a>
        </li>
        <li>
          <a href="">Archive</a>
        </li>
      </ul>
    </div>
  );
};
